import React, {useEffect} from 'react';
import logoT from '../assets/MoMoWorksLogo.png';
import logoM from '../assets/0xgpt_logoF.svg';
import AOS from 'aos';
import 'aos/dist/aos.css';
import TwitterIcon from '../assets/twitter.svg';
import TelegramIcon from '../assets/telegram.svg';
import MediumIcon from '../assets/medium.svg';
import { Link } from 'react-router-dom';
import TelegramIcon2 from '@mui/icons-material/Telegram';

const Topbar = () => {
    useEffect(() => {
        AOS.init({duration: 1000});
      }, [])

    return (
        <div>
           <div className='topbar' data-aos="fade-down">
            <div><img className='topLogo' src={logoT} alt='logo' /></div>

           <Link to='https://t.me/MoMo_Works' target='blank_'><button  className='topbutton'>Write me<TelegramIcon2 style={{marginLeft:'0.2em'}} /></button></Link>
           
           </div>

        </div>
    );
};

export default Topbar;